<template>
  <div>
    <h1>회원가입</h1>
    <form @submit.prevent="signup">
      <input v-model="username" placeholder="Username" required />
      <input v-model="email" placeholder="Email" required />
      <input v-model="password" type="password" placeholder="Password" required />
      <input v-model="recommenderId" placeholder="추천인 ID (선택)" /> <!-- 추천인 ID 입력 필드 추가 -->
      <button type="submit">회원가입</button>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'UserSignup',
  data() {
    return {
      username: '',
      email: '',
      password: '',
      recommenderId: '',  // 추천인 ID 추가
    };
  },
  methods: {
    async signup() {
      try {
        // 서버로 보낼 데이터 생성 (추천인 ID가 있을 경우에만 포함)
        const data = {
          username: this.username,
          email: this.email,
          password: this.password,
        };
        if (this.recommenderId) {
          data.recommender_id = this.recommenderId;  // 추천인 ID가 있을 경우에만 포함
        }

        // 회원가입 API 요청
        await axios.post('http://localhost:3000/signup', data);
        alert('회원가입 성공');
      } catch (error) {
        alert('회원가입 실패');
        console.error('회원가입 오류:', error);
      }
    },
  },
};
</script>
